.page-title {
  color: #aee3fe;
  text-align: center;
  position: relative;
  top: 60px;
  font-size: 26px;
}
.login-wrapper {
  position: relative;
  top: 160px;
}
.login-wrapper .login-form {
  border: 1px solid #0c426f;
  padding: 46px;
}
.login-wrapper .login-form .login-title {
  color: #3e82e7;
  text-align: center;
  margin-bottom: 30px;
}
.login-wrapper .login-form .username {
  color: #3d6983;
  outline: none;
  border-color: #3d6983;
  background-color: transparent;
}
.login-wrapper .login-form .username.ant-input-affix-wrapper > input.ant-input {
  background-color: transparent;
  color: #3d6983;
  caret-color: #3d6983;
}
.login-wrapper .login-form .username.ant-input-affix-wrapper > input.ant-input::-webkit-input-placeholder {
  color: #3d6983;
}
.login-wrapper .login-form .username input:-internal-autofill-previewed,
.login-wrapper .login-form .username input:-internal-autofill-selected {
  -webkit-text-fill-color: #3d6983 !important;
  transition: background-color 5000s ease-in-out 0s !important;
}
.login-wrapper .login-form .username input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px transparent inset !important;
}
.login-wrapper .login-form .username input:-webkit-autofill:focus {
  box-shadow: 0 0 0px 1000px transparent inset !important;
}
#tsparticles {
  position: fixed;
  top: 0;
  z-index: -99999;
}
