.default-box-wrapper {
  display: inline-block;
  position: relative;
}
.default-box-title {
  border: solid 1px #003e4a;
  background: #002f39;
  height: 40px;
  line-height: 40px;
  color: #dafef9;
  text-align: center;
  font-size: 16px;
  width: 100%;
}
.default-box-body {
  border: solid 1px #003e4a;
  background: #00212f;
  width: 100%;
}
